import React from 'react';

import withHelmet from '../hocs/withHelmet';
import homeImage from './home.jpg';

function Home() {
  return (
    <div>
      <h3>Home</h3>
      <img src={homeImage} alt="home" />
    </div>
  );
}

export default withHelmet({ title: 'Home' })(Home);



